import React, { useState } from "react";
import "./styles.css";

var emojiDictionary = {
  "üçá": "Grapes",
  "üçà": "Melon",
  "üçâ": "Watermelon",
  "üçä": "Tangerine",
  "üçã": "Lemon",
  "üçå": "Banana",
  "üçç": "Pineapple",
  "ü•≠": "Mango"
};
var trending = {
  "‚ù§Ô∏è": "Red Heart",
  "‚ú®": "Sparkles",
  "ü•∫": "Pleading Face",
  "üî•": "Fire",
  "üòÇ": "Face with Tears of Joy",
  "üòä": "Smiling Face with Smiling Eyes",
  "‚úîÔ∏è": "Check Mark",
  "ü•∞": "Smiling Face with Heart"
};

// How to convert object to arrays in react
// console.log(Object.keys(emojiDictionary));

var emojisWeKnow = Object.keys(emojiDictionary);
const trendingEmoji = Object.keys(trending);

export default function App() {
  const [meaning, setMeaning] = useState("");
  const [trendMeaing, setTrendMeaning] = useState("");

  function emojiInputHandler(event) {
    const userInput = event.target.value;
    var meaning = emojiDictionary[userInput];

    if (meaning === undefined) {
      meaning = "This emoji is not in our database";
    }
    setMeaning(meaning);
  }

  function clickedEmoji(emojiList) {
    const meaning = emojiDictionary[emojiList];
    setMeaning(meaning);
    const trendMeaing = trending[emojiList];
    setTrendMeaning(trendMeaing);
  }

  return (
    <div className="heading">
      <h1>Emoji finder</h1>
      <input
        className="input-tag"
        placeholder="Please enter your emoji here!!"
        onChange={emojiInputHandler}
      ></input>
      <div className="display">
        {meaning} {trendMeaing}
      </div>

      <hr />
      <h2
        style={{
          width: "15%"
        }}
        className="head-tag"
      >
        Emojis we knew
      </h2>
      <br />
      {emojisWeKnow.map(function (emojiList) {
        return (
          <span
            className="emoji"
            key={emojiList}
            onClick={() => clickedEmoji(emojiList)}
          >
            {emojiList}
          </span>
        );
      })}

      <hr />

      <h2
        style={{
          width: "10%"
        }}
        className="head-tag"
      >
        Trending
      </h2>

      <br />

      {trendingEmoji.map((trending) => {
        return (
          <span
            className="emoji"
            key={trending}
            onClick={() => clickedEmoji(trending)}
          >
            {trending}
          </span>
        );
      })}
    </div>
  );
}
